$red: #dde891;

$primary: #006AE7;
$secondary: #0BB4FE;
$w100: white;
$text: #262626;
$g50: #667085;
$b100: #000;

// Spacing
$s4: 4px;
$s8: 8px;
$s12: 12px;
$s16: 16px;
$s20: 20px;
$s24: 24px;
$s28: 28px;
$s32: 32px;
$s36: 36px;
$s40: 40px;
$s44: 44px;
$s48: 48px;

html {
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Plus Jakarta Sans', sans-serif;
}

p {
  margin: 0;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}


$xs: 0px;
$sm: 768px;
$md: 992px;
$lg: 1280px;
$xl: 1440px;
$xxl: 1920px;

.container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-flow: dense;
  grid-gap: 24px;
  margin-right: auto;
  margin-left: auto;
  max-width: 100%;
  padding-left: 24px;
  padding-right: 24px;
  
  @media (min-width: $sm) {
    grid-template-columns: repeat(8, 1fr);
    padding-left: 32px;
    padding-right: 32px;
  }

  @media (min-width: $md) {
    grid-template-columns: repeat(12, 1fr);
    max-width: 840px;
    padding-left: 0;
    padding-right: 0;
  }

  @media (min-width: $lg) {
    max-width: 100%;
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (min-width: $xl) {
    padding-left: 60px;
    padding-right: 60px;
  }

  @media (min-width: $xxl) {
    max-width: 1560px;
    padding-left: 0;
    padding-right: 0;
  }
}

@for $i from 1 through 4 {
  @media (min-width: $xs) {
    .col-xs-#{$i} {
      grid-column: span #{$i}; 
    }
  }
}

@for $i from 1 through 8 {
  @media (min-width: $sm) {
    .col-sm-#{$i} {
      grid-column: span #{$i}; 
    }
  }
}


@for $i from 1 through 12 {
  @media (min-width: $md) {
    .col-md-#{$i} {
      grid-column: span #{$i}; 
    }
  }
}

.none {
  display: none;
}

.sm-none {
  @media (min-width: $sm) {
    display: none;
  }
}

.md-none {
  @media (min-width: $md) {
    display: none;
  }
}

.block {
  display: block;
}

.sm-block {
  @media (min-width: $sm) {
    display: block;
  }
}

.md-block {
  @media (min-width: $md) {
    display: block;
  }
}

.text {
  color: $text;
  font-size: 19px;
  line-height: 171%; /* 32.49px */
}

.btn {
  display: inline-flex;
  height: 40px;
  padding: 0 16px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border: none;
  border-radius: 12px;
  font-family: Plus Jakarta Sans;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.5;
  letter-spacing: -0.6px;
  transition: all .35s ease;
  white-space: nowrap;
  
  &-clean {
    background-color: $w100;
    color: $primary;

    &:hover {
      opacity: 0.8;
    }

    &.secondary {
      background-color: $w100;
      color: $secondary;
    }
  }

  &-fill {
    background-color: $primary;
    color: $w100;

    &:hover {
      opacity: 0.8;
    }

    &.secondary {
      background-color: $secondary;
      color: $w100;
    }
  }

  &-outlined-white {
    border: 1px solid $w100;
    background-color: transparent;
    color: $w100;

    &:hover {
      background-color: $w100;
      color: $text;
    }
  }


  @media (min-width: $sm) {
    height: 50px;
    padding: 0 32px;
    font-size: 20px;
  }

  @media (min-width: $md) {
    height: 62px;
    padding: 0 60px;
  }
}



